a {
  text-decoration: none;
  color: #000;
}

h1 {
  font-size: 3.25rem;
  transition: 0.25s ease-in-out;
}

h2 {
  font-size: 2.75rem;
  transition: 0.25s ease-in-out;
}

h3 {
  font-size: 2.25rem;
}

h4 {
  font-size: 1.25rem;
  line-height: 2.75rem;
}

h5 {
  font-size: 1.25rem;
  line-height: 2.75rem;
  font-weight: 400;
}

p {
  font-size: 1.125rem;
  line-height: 2.75rem;
}

.light {
  color: white;
}

.dark {
  color: black;
}

.grey {
  /* color: #999; */
  opacity: 0.4;
}

.number {
  width: 40px;
  height: 40px;
  text-align: center;
  border: 1px #000 solid;
  float: left;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.number-light {
  width: 40px;
  height: 40px;
  text-align: center;
  border: 1px #fff solid;
  float: left;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 600px) {
  h1 {
    font-size: 2.5rem;
  }
}

@media only screen and (max-width: 768px) {
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.75rem;
  }

  h4,
  h5 {
    font-size: 1rem;
  }

  p {
    font-size: 1rem;
    line-height: 2rem;
  }
}

@media only screen and (min-width: 600px) and (max-width: 768px) {
  h1 {
    font-size: 2.75rem;
  }
}
