@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@500;600;700&display=swap');

* {
  font-family: 'Manrope', sans-serif;
  box-sizing: border-box;
  margin: 0;
}

.app {
  /* max-width: 1440px; */
  width: 100vw;
  margin: 0 auto;
  min-width: 375px;
  overflow-x: hidden;
}

.logo {
  height: 1rem;
}

.dark-logo {
  display: none;
  height: 1rem;
  text-align: left;
}

img {
  border-radius: 7px;
}

/* Navigation */

.nav {
  padding: 20px 100px;
  display: inline-flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1001;
  align-items: center;
  min-width: 375px;
  width: 100%;
}

.nav-menu {
  width: 100%;
  max-width: 200px;
  display: flex;
  justify-content: space-between;
  margin-left: 50px;
}

.nav-item {
  font-size: 1.125rem;
  display: inline-block;
  color: #fff;
  font-weight: 600;
}

.nav-item:hover {
  color: #ccc;
  border-bottom: #666 2px solid;
}
/* Page */

.section {
  padding-top: 200px;
  padding-bottom: 200px;
  width: 100%;
  grid-column: span 12;
}

.section-100-both {
  padding-top: 100px;
  padding-bottom: 100px;
  width: 100%;
  grid-column: span 12;
}

.section-100 {
  padding-top: 100px;
  width: 100%;
  grid-column: span 12;
}

.section-noP {
  padding-top: 150px;
  width: 100%;
  grid-column: span 12;
}

.container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 2rem;
  width: 100%;
}

.padding-h {
  padding-left: 100px;
  padding-right: 100px;
}

/* Header */

.header {
  background-color: black;
  color: #fff;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-size: cover;
  background-position: bottom right;
}

.header-navspace {
  width: 100%;
  height: 64px;
}

.header-text {
  width: 100%;
  /* margin: 0 auto; */
}

.header-title {
  filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.4));
}

/* Components */
button {
  padding: 10px 20px;
  background: none;
  border-radius: 100px;
  font-size: 1rem;
  border: 1px #000 solid;
  color: #000;
  margin: 20px 0;
  cursor: pointer;
}

.button-light {
  border: 1px #fff solid;
  color: #fff;
}

.middle {
  text-align: center;
}

.try-btn {
  display: block;
  margin: 10px auto;
}

.button-light:hover {
  background-color: #fff;
  color: #000;
}

button:hover {
  background-color: black;
  color: #fff;
}

/* footer */

footer {
  width: 100%;
}

.email-wrapper {
  grid-column: span 12;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #f0f0f0;
}

.email-paragraph {
  margin-top: 20px;
}

.footer {
  background-color: black;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.social-icon {
  width: 30px;
  margin-right: 30px;
  transition: 0.25s ease-out;
}

.social-icon:hover {
  opacity: 0.5;
}

.email {
  transition: 0.25s ease-out;
  padding: 1px;
}

.email:hover {
  border-bottom: 1px #fff solid;
}

.footer-contacts,
.footer-copyright {
  grid-column: span 12;
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  align-items: center;
}

.footer-copyright {
  font-size: 0.9rem;
  color: #ccc;
}

/* Project Cards */

.projectCard {
  padding-top: 75px;
  padding-bottom: 75px;
  border-top: 1px #ccc solid;
  background-color: #fff;
  perspective: 8px;
  perspective-origin: 50%;
}

.projectCard-label {
  margin-bottom: 100px;
}

.arrow-icon {
  width: 50px;
  float: right;
}

.projectCard-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.projectCard-link {
  border-bottom: 2px #000 solid;
}

.projectCard-link:hover {
  color: #777;
  border-bottom: 2px #777 solid;
}

.projectCard:hover {
  border-top: 1px #bbb solid;
}

#doodlevr:hover {
  background: url('/img/doodle-thumbnail.png') no-repeat;
  background-size: cover; /* Resize the background image to cover the entire container */
  background-position: 0px 80%;
  opacity: 0.9;
  background-color: #137cff;
}

#bridge:hover {
  background: url('/img/bridge-thumbnail.png') no-repeat;
  background-size: cover; /* Resize the background image to cover the entire container */
  background-position: 0px 60%;
  opacity: 0.9;
  background-color: #00dba6;
}

#stellar:hover {
  background: url('/img/stellar-thumbnail.png') no-repeat;
  background-size: cover; /* Resize the background image to cover the entire container */
  background-position: 0px 55%;
  opacity: 0.9;
  background-color: #5cd9cf;
}

.projectCard-text {
  margin: 5px 0;
}

/* Challenge Card */

.challengeCard {
  padding-top: 150px;
  padding-bottom: 150px;
  background-color: #f0f0f0;
}

.challengeCard-subtitle {
  grid-column: span 12;
}

.challengeCard-title {
  grid-column: span 12;
}

.col-12 {
  grid-column: span 12;
}

.col-4 {
  grid-column: span 4;
}

.project-heading {
  margin-bottom: 50px;
  grid-column: span 12;
}

.col-8 {
  grid-column: span 8;
}

.col-10 {
  grid-column: span 10;
}

.col-6 {
  grid-column: span 6;
}

.mb-25 {
  margin-bottom: 25px;
}

.mb-50 {
  margin-bottom: 50px;
}

.mb-75 {
  margin-bottom: 75px;
}

.mb-100 {
  margin-bottom: 100px;
}

.mt-25 {
  margin-top: 25px;
}

.mt-50 {
  margin-top: 50px;
}

.mt-75 {
  margin-top: 75px;
}

.mt-100 {
  margin-top: 100px;
}

.black-bg {
  background-color: #000;
}

.blue-bg {
  background-color: #137cff;
}

.green-bg {
  background-color: #00dba6;
}

.grey-bg {
  background-color: #f0f0f0;
}

.ctr {
  align-self: center;
}

.project-photo {
  width: 100%;
  object-fit: contain;
}

.project-photo-padding {
  width: 60%;
  object-fit: contain;
  margin: 0 auto;
}

.row {
  width: 100%;
  display: block;
  float: left;
  margin-top: 25px;
}

.wrapper {
  margin-bottom: 200px;
}

.media {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2rem;
  grid-template-areas: 'media-desc media-photo';
}

.media.flipped {
  grid-template-columns: 2fr 1fr;
  grid-template-areas: 'media-photo media-desc';
}

.media-desc {
  grid-area: media-desc;
}

.media-photo {
  grid-area: media-photo;
}

.media-6 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  grid-template-areas: 'media-6-desc media-6-photo';
}

.media-6.flipped {
  grid-template-columns: 1fr 1fr;
  grid-template-areas: 'media-6-photo media-6-desc';
}

.media-6-desc {
  grid-area: media-6-desc;
}

.media-6-photo {
  grid-area: media-6-photo;
}

.moreProjectCard {
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: #ccc 1px solid;
}

.moreProjectCard:hover {
  background-color: #f0f0f0;
}

.moreProjectCard-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.more-arrow-icon {
  width: 30px;
  float: right;
}
.archive-nav {
  grid-column: span 12;
  height: 60px;
}

.tippt-link {
  color: #fff;
  padding-bottom: 0.25rem;
}

.tippt-link:hover {
  border-bottom: 2px solid white;
}

/* Media Queries */

@media only screen and (max-width: 400px) {
  .container {
    grid-column-gap: 0;
  }
  .nav {
    padding: 20px 30px;
  }
  .nav-menu {
    margin-left: 100px;
  }
  .padding-h {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media only screen and (min-width: 400px) and (max-width: 600px) {
  .nav {
    padding: 20px 50px;
  }
  .nav-menu {
    margin-left: 100px;
  }

  .padding-h {
    padding-left: 50px;
    padding-right: 50px;
  }
}

@media only screen and (max-width: 600px) {
  .header {
    padding-top: 50px;
  }

  .arrow-icon {
    width: 30px;
    height: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .grid-full {
    margin-top: 25px;
  }

  .section {
    padding-top: 150px;
    padding-bottom: 150px;
  }

  .col-4,
  .col-6,
  .col-8,
  .col-9,
  .col-10 {
    grid-column: span 12;
  }

  .media {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-template-areas:
      'media-desc'
      'media-photo';
  }

  .media-6 {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-template-areas:
      'media-6-desc'
      'media-6-photo';
  }

  .media.flipped {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-template-areas:
      'media-desc'
      'media-photo';
  }

  .media-6.flipped {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-template-areas:
      'media-6-desc'
      'media-6-photo';
  }

  .media-desc,
  .media-photo,
  .media-6-desc,
  .media-6-photo {
    grid-column: span 1;
  }

  .wrapper {
    margin-bottom: 100px;
  }

  .project-photo-padding {
    width: 80%;
    object-fit: contain;
  }
}

@media only screen and (min-width: 600px) and (max-width: 768px) {
  .nav {
    padding: 20px 75px;
  }

  .padding-h {
    padding-left: 75px;
    padding-right: 75px;
  }

  .footer-contacts,
  .footer-copyright {
    grid-column: span 12;
    display: flex;
    flex-direction: column;
    margin: 10px 0;
    align-items: center;
  }

  .social-icon {
    margin: 10px 20px;
  }
}
